
.btns {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin-bottom: 10px;  
}
.Rview {
  border: 1px solid grey;
  width: 150px;
  height: 100px;
  position: absolute;
  top: 50px;
  right: 5px;
  overflow: auto;
  /* overflow-y: scroll; */
}
p.Rview_title {
  margin-top: 2px;
  margin-bottom: 2px;

}
.off {
  display: none;
}
.rview_item {
  color: powderblue;
}
.YelloBtn:hover {
  background-color: rgb(0, 119, 255);
  color: white;
  transform: scale(1.03);
  transition: all 0.1s;
  /* border: 0px */
}
.Rview ul {
  list-style: none;
  text-align: left;
  text-decoration-line: none; 
  left: -10p;
  padding-left: 20px;
} 
.Rview ul a {
  list-style: none;
  text-align: left;
  text-decoration-line: none;  
}
div.Rview ul li>a:hover {
  color: rgb(202, 82, 82);
}
.start {
  opacity: 0;
  transform: scale(0);
}
.end {
  opacity: 1;
  transform: scale(1);
  transition: all 0.1s;
}
.Card:hover {
  cursor: pointer;
  transform: scale(1.1);
  transition: all 0.3s; 
  color: palevioletred; 
}
.footer {
  background-color: black;
  height: 270px; 
  color: white;
}
.copy {
  position: relative;
  top: 235px;
}
.event_btn {
  border: 0px solid black;
  background-color: powderblue;  
}
.event_btn:hover {
  border: 1px;
  color: white;
  background-color: rgb(0, 119, 255);
  transform: scale(1.1);
  transition: all 0.1s;
}
.event {
  color: white;
}
.eventItem {
  background-color: palevioletred;
  color: white;
  border: 1px
}
.eventItem:hover {
  background-color: palegreen;
  color: palevioletred;
  transform: scale(1.2);
  transition: all 0.15s;
}
.st {
  font-size: 8px;
}
.box {
  background-color: palevioletred;
  width: 350px;
  height: 200px;
  margin: 0 auto;
  margin-top: 30px;
  border: 10px solid rgba(226, 202, 262, 90);  
}
.notAccess {
  width: 300px;
  height: 350px;
  margin-left: 30px;
  background-image: url('./img/notaccess.jpg');
  background-size: cover;
  background-position: center;
  position: relative;
  margin-right: 0px;
  margin-bottom: 30px;
}
.mypage {
  width: 360px;
  height: 600px;
  margin-left: 30px;
  background-image: url('./img/mypage.png');
  background-size: cover;
  background-position: center;
  position: relative;
  margin-left: 0px;
  margin-bottom: 10px;
}
.main-bg {
  height: 300px;
  background-image: url('./img/bg.png');
  background-size: cover;
  background-position: center;
  position: relative;
}
.logo {
  cursor: pointer;
}
.logo:hover {
  color: grey;
}
.logo:active {
  color: palevioletred;
}
.App {
  text-align: center;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
